/* Table of Contents:
	1.0 Reset Styles
	2.0 Baseline (Default) Styles
	3.0 Column Structure
	4.0 Main Column (articles, etc)
	5.0 Comments and Pingbacks
	6.0 Sidebar lists and Ads
	7.0 Footer 
*/

@import 'normalize.css';

/* Buddypress reset */
.buddybuildr div, 
.buddybuildr dl, 
input[type="reset"], 
input[type="search"], 
input[type="submit"], 
.buddybuildr li, 
.buddybuildr select, 
.buddybuildr textarea {
	border-radius: unset;
}

/*==================================
=            3.0 ELEMENTS            =
==================================*/

.buddybuildr .fa {
	font: normal 900 14px 'Font Awesome 5 Free';
	font-family: 'Font Awesome 5 Free';
}

hr {
	background-color: #eaeaea;
	background-color: rgba(51, 51, 51, 0.1);
	border: 0;
	height: 1px;
	margin-bottom: 1.6em;
}

ul,
ol {
	margin: 0 0 1.6em 1.3333em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
}

dl {
	margin-bottom: 1.6em;
}

dt {
	font-weight: bold;
}

dd {
	margin-bottom: 1.6em;
}

table,
th,
td {
	border: 1px solid #eaeaea;
	border: 1px solid rgba(51, 51, 51, 0.1);
}

table {
	border-collapse: separate;
	border-spacing: 0;
	border-width: 1px 0 0 1px;
	margin: 0 0 1.6em;
	table-layout: fixed; /* Prevents HTML tables from becoming too wide */
	width: 100%;
}

caption,
th,
td {
	font-weight: normal;
	text-align: left;
}

th {
	border-width: 0 1px 1px 0;
	font-weight: 500;
}

td {
	border-width: 0 1px 1px 0;
}

th, td {
	padding: 0.4em;
}

img {
	-ms-interpolation-mode: bicubic;
	border: 0;
	height: auto;
	max-width: 100%;
	vertical-align: middle;
}

figure {
	margin: 0;
}

del {
	opacity: 0.8;
}

::-webkit-input-placeholder {
	color: rgba(51, 51, 51, 0.7);
	font-family: "Roboto", sans-serif;
}

:-moz-placeholder {
	color: rgba(51, 51, 51, 0.7);
	font-family: "Roboto", sans-serif;
}

::-moz-placeholder {
	color: rgba(51, 51, 51, 0.7);
	font-family: "Roboto", sans-serif;
	opacity: 1; /* Since FF19 lowers the opacity of the placeholder by default */
}

:-ms-input-placeholder {
	color: rgba(51, 51, 51, 0.7);
	font-family: "Roboto", sans-serif;
}

.buddybuildr .fa:before {
	margin-top: 1px; 
	width:30px; 
	float:left;
}

.comment-respond {
	padding-top: 20px;
	padding-right: 5px;
}

time.updated.published:before {
    content: none;
}

time.updated:before {
    content: '. Updated ';
}

.post-navigation span {
    display: block;
}

textarea {
    background-color: #f1f1f1;
    border: 1px solid #ccc;
    padding: 8px;
}

/*textarea:focus {
    background-color: #fff;
    border: 1px solid #ccc;
}*/

input, textarea {
	box-sizing: border-box;
}

textarea:focus, input[type="email"]:focus, 
input[type="password"]:focus, input[type="search"]:focus, 
input[type="tel"]:focus, input[type="text"]:focus, input[type="url"]:focus {
    outline: none;
}

/*==================================
=            3.0 FOOTER            =
==================================*/

#colophon {
    text-align: center;
    color: #757575;
    background: #efefef;
	border-top: 1px solid #ccc;
    display: block;
    display: table;
    width: 100%;
}

#colophon p, #colophon ul {
	margin: 0;
	text-align: center;
	font-size: 16px;
	font-weight: 300;
}

.footer-nav-container, .site-info {
	width: 100%;
}

#colophon ul {
	list-style: none;
}

#colophon ul a {
	text-decoration: none;
}

#colophon ul a:hover {
	text-decoration: underline;
}

#colophon ul.footer-menu {
	margin: 15px;
}

.footer-nav-container ul li {
	list-style: none;
	text-align: center;
	padding: 0 20px;
	display: inline-block;
}

/*==================================
=            4.0 ACCESSIBILITY            =
==================================*/

/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.screen-reader-text:hover,
.screen-reader-text:active,
.screen-reader-text:focus {
  background-color: #f1f1f3;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/*==================================
=            5.0 CONTENT            =
==================================*/

html, body {
	height: 100%;
	background-color: #fafafa;
}

.site {
	background-color: #f1f1f1;
	min-height: 100%;
}

.site-content {
    padding-bottom: 30px;
}

#wrapper, #page {
    transition: margin-right .5s;
}

#sidebar {
	padding-bottom: 30px;
}


/*
   Templates
   ========================================================================== */

.page-template-fullwidth .entry-header {display:none;}
.page-template-fullwidth main, .page-template-search main {
	padding: 0;
}

.post-template-default main, .page-template-default main, .search-results main, .search-no-results main {
	max-width: 800px;
	margin: 0px auto 50px auto;
	border-radius: 4px;
}

.page-template-sidebar main {
	max-width: 700px;
	margin: auto;
}

.page-template-sidebar #primary {
	background-color: #fff;
}

main {
    overflow: hidden;
    padding: 20px;
}
