/*==================================
=            6.0 HEADER            =
==================================*/

.site-header {
	box-shadow: 0 1px 8px rgba(0,0,0,.3);
	z-index: 9;
	-webkit-transition: all 0.5s ease;
	-moz-transition: position 10s;
	-ms-transition: position 10s;
	-o-transition: position 10s;
	transition: all 0.5s ease;
}

.site-header p, .site-header h3 {
	margin: 0;
}

.site-header a {
	text-decoration: none;
	color: #ffffff; /*default*/
	border-right: 4px;
}

.site-header a:hover {
	background-color: rgba(0,0,0,0.071); 
	border-radius:4px;
}

/*.home .site-header .home-icon {
	background-color: rgba(0,0,0,0.071); 
	border-radius:4px;
}*/

/*.home .site-header .home-icon:after {
	content: '\2022';
	color: #fff;
}*/

.home .site-header .home-icon:after {
	content: '\26AB';
	color: rgba(0, 0, 0, 0.17);
}

.site-header .username {
	cursor: pointer;
	text-transform: capitalize;
}

.site-header .username a {
	padding: 10px;
	display: inline-block;
}

.site-branding a:hover {
	background: transparent;
}
/*.site-header .username a:hover {
	background-color: rgba(0,0,0,0.071);
}*/

.header-wrapper {
	overflow: hidden;
	font-size: 16px;
	padding: 0 10px;
	border-radius: 0px;
}

/*.site-header-item {
	color: #ffffff; //default
	margin:10px; 
	line-height:20px;
	display: inline-block;
}*/

.site-header-item.social-area-icons {
	margin: 9px;
}

.site-title {
    display: inline-block;
    padding: 10px;
}

.site-branding {
    /*display: inline-block;*/
    float: left;
    margin-left: 0px;
}

.site-branding img {
	height: 40px;
}

/* Makes Header Transparent */

.transparent-header .site-header {
	position: absolute;
	width: 100%;
	z-index: 2;
}

.transparent-header #primary {
	position: relative;
	z-index: 1;
}

.transparent-header .site-header .header-wrapper {
	background: transparent;
}

.header-rightblock-hidden .header-wrapper .header-right {
	display: none;
}

.header-branding-hidden .header-wrapper .site-branding {
	display: none;
}


