/*==================================
=            11.0 SEARCH            =
==================================*/

/* Style the search field */
.buddybuildr input:not([type=submit]):not([type=file]) {
  padding: 10px;
  font-size: 17px;
  box-sizing: border-box;
  border-radius: 5px;
}

input[type="text"], input[type="password"], input[type="email"],
input[type="url"], input[type="date"], input[type="month"], 
input[type="time"], input[type="datetime"], input[type="datetime-local"], input[type="week"],
input[type="number"], input[type="search"], input[type="tel"], input[type="color"], select, .buddybuildr textarea {
	-moz-appearance: none;
	background: #fff;
	border: .1rem solid #dcdfe2;
	border-radius: 5px;
	box-shadow: none;
	display: block;
	margin: 0;
    margin-bottom: 0px;
    max-width: 100%;
    outline: none;
    word-break: normal;
}

input[type="text"], input[type="email"], input[type="url"], input[type="password"], input[type="search"], textarea {
    width: 100%;
}

/* Clear floats */
form::after {
  content: "";
  clear: both;
  display: table;
}

/*clearing both*/
.clearfix {
	clear: both;
}

.side-search {padding-top:20px;}
.side-search .search-wrap, .side-search .search-wrap input[type="text"], .side-search .search-wrap input[type="search"],
.side-search input[type="text"], .side-search input[type="search"] {
	width: 100%;
	max-width: 200px;
	margin: auto;
}
.side-search input[type="text"], .side-search input[type="search"] {
		padding-right: 10px;
}

.side-search input[type=submit], .search-wrap input[type=submit] {
	display:none;	
}

#SearchIcon {
	line-height: 17px;
	/*display: inline-block;*/
	float: right;
}

.side-search form input[type=text] {float:none;}
.side-search input[type=text]:focus, .side-search input[type=search]:focus { 
	border: 1px solid #ccc;
	box-shadow: 0 1px 1px rgba(0,0,0,0.24);
}
.side-search form div {text-align:center; position: relative; overflow: hidden;}
#SearchIcon .fa-search::before {
    position: relative;
    top: 1px;
}

.search-wrap {position: relative;}
.buddybuildr .search-wrap input[type="search"] { 
	text-indent: 32px;
	border: none;
	border-radius: 4px;
	/*padding: 5px;
	background: #f1f1f1;*/
}

.search-wrap:before {
    font-family: 'Font Awesome 5 Free';
    color:#ccc;
    position: absolute;
    left: 10px;
	top: 7px;
    content: "\f002";
	font-size: 20px;
	font-weight: 900;
}

.search-wrap input::placeholder {
	color: #ccc;
	font-weight: 100;
}

.search-wrap input:focus {
	border: 1px solid #ccc;
  	box-shadow: 0 1px 1px rgba(0,0,0,0.24);
}
/*==================================
=            12.0 BLOG            =
==================================*/

/*#primary main {
	padding: 30px;
}*/

#primary {
	margin-bottom: -1px;
}

.grid-item a {
	text-decoration: none;
}

.grid-item .title {
	display: block;
	line-height: 32px;
	margin-bottom: 8px;
	font-size: 24px;
}

.grid-item .posted-on {
	display: inline-block;
	overflow: hidden;
	color: #848484;
}

.grid-item .byline {
	display: inline-block;
	width: 100px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	color: #848484;
}

.grid-item .byline a {
	color: #848484;
}

.grid-item-content {
	background-color: #fff;
	padding: 24px;
}

.grid-item-content p {
	margin-bottom: 5px;
}

.grid-wrap {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-column-gap: 1rem;
	grid-row-gap: 1rem;
	margin-left: 0;
	list-style: none;
}

.blog .grid-wrap {
	margin-bottom: 0;
}

/*For all the pages */
#Dmasthead { grid-area: h; }
#Amasthead { grid-area: h; }
#primary { grid-area: m; }
#secondary { grid-area: s; background-color: #e9e9e9; }
#buddypress-sidebar { grid-area: b; background-color: #e9e9e9; }
#colophon { grid-area: f; }
#bb_home_search_container { grid-area: sc; } /* sc to mean search container */

.post-template-default #buddypress-sidebar {
	display: none;
}

body:not(.buddypress) #buddypress-sidebar {
	display: none;
} 


.grid-container > * {
	min-width: 0;
	overflow: hidden;
}

.grid-container {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto auto 1fr auto auto;
  grid-template-areas: "h"
                       "m"
                       "s"
                       "f";
}

/* For Search Pages */

body.page-template-search .grid-container {
	min-height: 100vh;
	display: grid;
	grid-template-columns: 100%;
	grid-template-rows: auto auto 1fr auto auto;
	grid-template-areas: "h"
  						 "sc"
                       	 "m"
                         "s"
                         "b"
                         "f";
}


/*For BuddyPress pages */

body.buddypress .grid-container {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto auto 1fr auto auto;
  grid-template-areas: "h"
                       "m"
                       "s"
                       "b"
                       "f";
}

@media (min-width: 768px) {

	body.buddypress .grid-container {
		    grid-template-columns: 250px 1fr;
		    grid-template-rows: auto 1fr auto;
		    grid-template-areas: "h h"
		                         "s m"
		                         "f f";
	}
}

@media (min-width: 786px) {
	body.buddypress .grid-container {
		grid-template-columns: 300px 1fr;
	}
}

@media only screen and (min-width: 620px) {
	.bb_home_search_container .home-sc {
		width: 450px;
		max-width: none;
	}
	.inside-article {
		padding: 30px;
	}
	.buddybuildr .buddypress-wrap .subnav-filters .component-filters {
		margin-left: 20px;
	}
}

@media (min-width: 768px) {
	#bb_home_search_container h2, #bb_home_search_container h3 {
		max-width: 80%;
	}
  .single .grid-container, .post-template-default .grid-container, .page-template-sidebar .grid-container {
    grid-template-columns: 250px 1fr;
    grid-template-rows: auto 1fr auto;
    grid-template-areas: "h h"
                         "s m"
                         "f f";
  }
  .grid-container {
    grid-template-columns: 100%;
    grid-template-rows: auto 1fr auto;
    grid-template-areas: "h"
                         "m"
                         "f";
  }

  body.page-template-search .grid-container {
    grid-template-columns: 100%;
    grid-template-rows: auto auto 1fr auto;
    grid-template-areas: "h"
    					 "sc"
                         "m"
                         "f";
  }

  #item-header-cover-image #item-header-avatar {
	padding-left: 1em;
  }

  main {
  	padding: 5% 20px;
  }

  .blog main, .archive main {
  	padding: 20px;
  	max-width: 1100px;
  	margin: auto;
  }

  #colophon .footer-nav-container, #colophon .site-info {
	width: 50%;
	float: left;
  }

  .dropdown .dropdown-content {
  	width: unset;
  	left: unset;
  }

  .bb_home_search_container .home-sc {
  	width: 560px;
  	max-width: none;
  }

  .page-template .bb_home_search_container { padding: 100px 50px 150px 50px; }

}

@media (min-width: 880px) {
	.single .grid-container .post-template-default .grid-container, .page-template-sidebar .grid-container {
		grid-template-columns: 300px 1fr;
	}

	.grid-container {
		grid-template-columns: 1fr;
	}
}

@media (min-width: 955px) {
	.single .grid-container, .post-template-default .grid-container, .page-template-sidebar .grid-container {
		grid-template-columns: 25% 1fr;
	}

	.grid-container {
		grid-template-columns: 1fr;
	}

	main {
  	padding: 3% 20px;
  }
}


