} */

.hidden {
    width:0%;
    background:#f90;
    color:#000;
}

.transition-right{
    
    transition: top 1.5s ease-out;  /* Chrome 26, Firefox 16+, IE 10+, Opera 12.10+ */

}

.transition-left{
    
    transition: top 1.5s ease-out;  /* Chrome 26, Firefox 16+, IE 10+, Opera 12.10+ */

}

@keyframes overflowhidden
{
    0%      {overflow: hidden;}
    100%    {overflow: hidden;}
}

.site-header-common, .header-site-search {
	position: relative;
}

.site-header-common {
	right: 0px;
	display: block;
	top: 0;
}

.hide-common {
    right: -100% !important;
	top: -100%;
    /* display: none !important; */
}

.header-site-search {
	/* display: none; */
	/*left: -100%;*/
}

.show-search {
	top: 0;
    left:0px !important;
    display: block !important;   
}

/* .site-header {
	overflow: hidden;
} */

.site-header:after {
	clear: both;
}

.sub-menu {
	display: none;
}

/* Customizer */

@media only screen and (min-width:899px) {
	#Dmasthead.full-header .site-header-main {
		height: 90px;
		padding: 25px 20px;
	}
	header#Dmasthead.full-header {height: 90px;}
	#Dmasthead.full-header .header-site-search.transition-left {
		top: -90px;
		padding: 25px 10px;
		height: 90px;
	}
}